import React, { useState, useEffect } from 'react'
import {
  View,
  Flex,
  Heading,
  Text,
  Button,
  ActionButton,
  Well,
  TextField,
  Dialog,
  DialogTrigger,
  Content,
  ButtonGroup
} from '@adobe/react-spectrum'
import chatStorage from '../services/chatStorage'

function ChatHistory({ isOpen, onClose, onSelectChat, currentChatId }) {
  const [chats, setChats] = useState([])
  const [searchTerm, setSearchTerm] = useState('')
  const [editingTitle, setEditingTitle] = useState(null)
  const [newTitle, setNewTitle] = useState('')

  useEffect(() => {
    if (isOpen) {
      loadChats()
    }
  }, [isOpen])

  const loadChats = async () => {
    try {
      const allChats = await chatStorage.getAllChats()
      setChats(allChats)
    } catch (error) {
      console.error('Error loading chats:', error)
      setChats([])
    }
  }

  const filteredChats = chats.filter(chat =>
    chat.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (chat.messages || []).some(msg => 
      msg.content && msg.content.toLowerCase().includes(searchTerm.toLowerCase())
    )
  )

  const handleSelectChat = (chat) => {
    onSelectChat(chat)
    onClose()
  }

  const handleDeleteChat = async (chatId) => {
    if (window.confirm('Are you sure you want to delete this chat?')) {
      try {
        await chatStorage.deleteChat(chatId)
        loadChats()
      } catch (error) {
        console.error('Error deleting chat:', error)
      }
    }
  }

  const handleEditTitle = (chat) => {
    setEditingTitle(chat.id)
    setNewTitle(chat.title)
  }

  const handleSaveTitle = async (chatId) => {
    if (newTitle.trim()) {
      try {
        await chatStorage.updateChatTitle(chatId, newTitle.trim())
        loadChats()
      } catch (error) {
        console.error('Error updating chat title:', error)
      }
    }
    setEditingTitle(null)
    setNewTitle('')
  }

  const handleCancelEdit = () => {
    setEditingTitle(null)
    setNewTitle('')
  }

  const formatDate = (dateString) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now - date
    const diffHours = diffMs / (1000 * 60 * 60)
    const diffDays = diffMs / (1000 * 60 * 60 * 24)

    if (diffHours < 1) {
      return 'Just now'
    } else if (diffHours < 24) {
      return `${Math.floor(diffHours)} hours ago`
    } else if (diffDays < 7) {
      return `${Math.floor(diffDays)} days ago`
    } else {
      return date.toLocaleDateString()
    }
  }

  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed': return '✅'
      case 'failed': return '❌'
      case 'in_progress': return '🔄'
      default: return '📱'
    }
  }

  if (!isOpen) return null

  return (
    <View
      UNSAFE_style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.6)',
        zIndex: 1000,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}
      onClick={onClose}
    >
      <div
        style={{
          width: '80%',
          maxWidth: '800px',
          height: '80%',
          maxHeight: '600px',
          padding: '24px',
          backgroundColor: 'white',
          borderRadius: '12px',
          boxShadow: '0 20px 30px rgba(0, 0, 0, 0.2)',
          display: 'flex',
          flexDirection: 'column'
        }}
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <Flex direction="row" justifyContent="space-between" alignItems="center" marginBottom="size-300">
          <Heading level={2}>Chat History</Heading>
          <ActionButton isQuiet onPress={onClose}>
            <svg width="20" height="20" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
          </ActionButton>
        </Flex>

        {/* Search */}
        <TextField
          label="Search chats"
          value={searchTerm}
          onChange={setSearchTerm}
          width="100%"
          marginBottom="size-300"
          placeholder="Search by title or content..."
        />

        {/* Chat List */}
        <View 
          flex={1}
          UNSAFE_style={{
            overflow: 'auto',
            border: '1px solid #E5E7EB',
            borderRadius: '8px',
            padding: '8px'
          }}
        >
          {filteredChats.length === 0 ? (
            <View
              UNSAFE_style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                height: '200px',
                textAlign: 'center'
              }}
            >
              <Text color="subdued">
                {searchTerm ? 'No chats found matching your search.' : 'No chat history yet. Start a conversation to see it here!'}
              </Text>
            </View>
          ) : (
            filteredChats.map((chat) => (
              <div
                key={chat.id}
                style={{
                  marginBottom: '8px',
                  cursor: 'pointer',
                  transition: 'background-color 0.2s',
                  backgroundColor: chat.id === currentChatId ? '#F0F9FF' : 'white',
                  border: chat.id === currentChatId ? '2px solid #3B82F6' : '1px solid #E5E7EB',
                  borderRadius: '8px',
                  padding: '16px'
                }}
                onClick={() => handleSelectChat(chat)}
              >
                <Flex direction="row" justifyContent="space-between" alignItems="flex-start" gap="size-200">
                  <Flex direction="column" flex={1} gap="size-100">
                    {/* Title */}
                    {editingTitle === chat.id ? (
                      <Flex direction="row" gap="size-100" alignItems="center">
                        <TextField
                          value={newTitle}
                          onChange={setNewTitle}
                          width="200px"
                          size="S"
                          autoFocus
                          onKeyDown={(e) => {
                            if (e.key === 'Enter') handleSaveTitle(chat.id)
                            if (e.key === 'Escape') handleCancelEdit()
                          }}
                        />
                        <ButtonGroup>
                          <ActionButton size="S" onPress={() => handleSaveTitle(chat.id)}>
                            ✅
                          </ActionButton>
                          <ActionButton size="S" onPress={handleCancelEdit}>
                            ❌
                          </ActionButton>
                        </ButtonGroup>
                      </Flex>
                    ) : (
                      <Flex direction="row" alignItems="center" gap="size-100">
                        <Text 
                          UNSAFE_style={{ 
                            fontSize: '16px', 
                            fontWeight: '600',
                            color: chat.id === currentChatId ? '#1E40AF' : '#374151'
                          }}
                        >
                          {getStatusIcon(chat.status)} {chat.title}
                        </Text>
                        {chat.id === currentChatId && (
                          <View
                            backgroundColor="blue-400"
                            borderRadius="medium"
                            paddingX="size-100"
                            paddingY="size-25"
                          >
                            <Text UNSAFE_style={{ fontSize: '10px', color: 'white', fontWeight: '600' }}>
                              CURRENT
                            </Text>
                          </View>
                        )}
                      </Flex>
                    )}

                    {/* Preview */}
                    <Text UNSAFE_style={{ fontSize: '12px', color: '#6B7280', lineHeight: '1.4' }}>
                      {chat.messages?.[0]?.content?.substring(0, 100) || 'No messages'}
                      {(chat.messages?.[0]?.content?.length || 0) > 100 ? '...' : ''}
                    </Text>

                    {/* Metadata */}
                    <Flex direction="row" gap="size-200" alignItems="center">
                      <Text UNSAFE_style={{ fontSize: '11px', color: '#9CA3AF' }}>
                        {formatDate(chat.lastModified)}
                      </Text>
                      <Text UNSAFE_style={{ fontSize: '11px', color: '#9CA3AF' }}>
                        {(chat.messages || []).length} messages
                      </Text>
                    </Flex>
                  </Flex>

                  {/* Actions */}
                  <Flex 
                    direction="row" 
                    gap="size-100"
                    onClick={(e) => e.stopPropagation()}
                  >
                    <ActionButton 
                      size="S" 
                      isQuiet 
                      onPress={() => handleEditTitle(chat)}
                      UNSAFE_style={{ opacity: 0.7 }}
                    >
                      ✏️
                    </ActionButton>
                    <ActionButton 
                      size="S" 
                      isQuiet 
                      onPress={() => handleDeleteChat(chat.id)}
                      UNSAFE_style={{ opacity: 0.7 }}
                    >
                      🗑️
                    </ActionButton>
                  </Flex>
                </Flex>
              </div>
            ))
          )}
        </View>

        {/* Footer */}
        <Flex direction="row" justifyContent="space-between" alignItems="center" marginTop="size-300">
          <Text UNSAFE_style={{ fontSize: '12px', color: '#6B7280' }}>
            {chats.length} chat{chats.length !== 1 ? 's' : ''} saved
          </Text>
          <Button variant="secondary" onPress={onClose}>
            Close
          </Button>
        </Flex>
      </div>
    </View>
  )
}

export default ChatHistory

