class MCPService {
  constructor() {
    this.baseUrl = '/api/mcp';
  }

  async getAvailableComponents() {
    try {
      const response = await fetch(`${this.baseUrl}/react-spectrum/list-components`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      return await response.json();
    } catch (error) {
      console.error('Error fetching components from MCP:', error);
      // Fallback to mock data if MCP server is not available
      return [
        {
          name: 'Button',
          category: 'Actions',
          description: 'A button allows a user to perform an action.'
        },
        {
          name: 'TextField',
          category: 'Forms',
          description: 'A text field allows a user to enter a plain text value with a keyboard.'
        },
        {
          name: 'Flex',
          category: 'Layout',
          description: 'Flex follows the CSS flexbox spec, useful for creating layouts.'
        },
        {
          name: 'Heading',
          category: 'Content',
          description: 'A heading represents a title or subtitle.'
        },
        {
          name: 'View',
          category: 'Layout',
          description: 'View is a general purpose container with no specific semantics.'
        },
        {
          name: 'TextArea',
          category: 'Forms',
          description: 'A text area allows a user to enter a multi-line plain text value.'
        }
      ];
    }
  }

  async getComponentDetails(componentName) {
    try {
      const response = await fetch(`${this.baseUrl}/react-spectrum/get-component`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ componentName })
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error fetching component details:', error);
      throw error;
    }
  }

  async searchComponents(query) {
    try {
      const response = await fetch(`${this.baseUrl}/react-spectrum/search-components`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ query })
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error searching components:', error);
      throw error;
    }
  }
}

export default new MCPService();