// Chat Storage Service - Manages chat history via Cloudflare Worker
class ChatStorageService {
  constructor() {
    this.workerUrl = 'https://llmo-chat-storage.philipp-koch.workers.dev'
    this.maxChats = 50
    this.maxAge = 30 * 24 * 60 * 60 * 1000 // 30 days in milliseconds
  }

  // Generate a simple title from the first prompt
  generateTitle(prompt) {
    if (!prompt || typeof prompt !== 'string') return 'New App'
    
    // Remove common words and extract key terms
    const commonWords = ['create', 'build', 'make', 'generate', 'a', 'an', 'the', 'with', 'using', 'for', 'and', 'or', 'but']
    const words = prompt
      .toLowerCase()
      .replace(/[^\w\s]/g, '')
      .split(' ')
      .filter(word => word.length > 2 && !commonWords.includes(word))
      .slice(0, 4)
    
    if (words.length === 0) return 'New App'
    
    // Capitalize first letter of each word
    const title = words
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ')
    
    return title.length > 30 ? title.substring(0, 30) + '...' : title
  }

  // Get all saved chats
  async getAllChats() {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats`)
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      return await response.json()
    } catch (error) {
      console.error('Error loading chat history:', error)
      return []
    }
  }

  // Get a specific chat by ID
  async getChat(chatId) {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/${chatId}`)
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      return await response.json()
    } catch (error) {
      console.error('Error loading chat:', error)
      return null
    }
  }

  // Save a new chat or update existing one
  async saveChat(chatData) {
    try {
      const chatToSave = {
        id: chatData.id,
        title: chatData.title || this.generateTitle(chatData.messages?.[0]?.content),
        createdAt: chatData.createdAt || new Date().toISOString(),
        lastModified: new Date().toISOString(),
        messages: chatData.messages || [],
        generatedApp: chatData.generatedApp || null,
        status: chatData.status || 'completed'
      }
      
      const response = await fetch(`${this.workerUrl}/api/chats`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(chatToSave)
      })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      
      return await response.json()
    } catch (error) {
      console.error('Error saving chat:', error)
      return null
    }
  }

  // Delete a chat
  async deleteChat(chatId) {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/${chatId}`, {
        method: 'DELETE'
      })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      
      const result = await response.json()
      return result.success
    } catch (error) {
      console.error('Error deleting chat:', error)
      return false
    }
  }

  // Update chat title
  async updateChatTitle(chatId, newTitle) {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/${chatId}/title`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ title: newTitle })
      })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      
      const result = await response.json()
      return result.success
    } catch (error) {
      console.error('Error updating chat title:', error)
      return false
    }
  }

  // Clear all chats
  async clearAllChats() {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/clear`, {
        method: 'DELETE'
      })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      
      const result = await response.json()
      return result.success
    } catch (error) {
      console.error('Error clearing chats:', error)
      return false
    }
  }

  // Export chats as JSON
  async exportChats() {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/export`)
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      return await response.text()
    } catch (error) {
      console.error('Error exporting chats:', error)
      return '[]'
    }
  }

  // Import chats from JSON
  async importChats(jsonData) {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/import`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ data: jsonData })
      })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      
      const result = await response.json()
      return result.success
    } catch (error) {
      console.error('Error importing chats:', error)
      return false
    }
  }

  // Get storage usage info
  async getStorageInfo() {
    try {
      const response = await fetch(`${this.workerUrl}/api/chats/info`)
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${await response.text()}`)
      }
      return await response.json()
    } catch (error) {
      console.error('Error getting storage info:', error)
      return { chatCount: 0, dataSize: 0, maxChats: this.maxChats, oldestChat: null }
    }
  }
}

// Export singleton instance
export default new ChatStorageService()

