import React, { useState, useRef, useEffect } from 'react'
import { 
  Flex, 
  View,
  TextArea, 
  Button, 
  Heading,
  Text,
  Well,
  ActionButton
} from '@adobe/react-spectrum'

function PromptPanel({ onGenerate, onFollowUp, isGenerating, currentChatId, chatHistory = [], availableComponents = [], retryStatus = {}, currentChatTitle = '', onNewChat, isLegacyChat = false }) {
  const [prompt, setPrompt] = useState('')
  const [showExamples, setShowExamples] = useState(false)
  const [retryingMessageIndex, setRetryingMessageIndex] = useState(null)
  const chatHistoryRef = useRef(null)
  const [examples] = useState([
    'Create a modern dashboard with interactive charts and user analytics',
    'Build a todo app with drag-and-drop functionality and local storage', 
    'Generate a responsive e-commerce product catalog with search, filters, and shopping cart'
  ])

  const handleSubmit = () => {
    console.log('🔘 Button clicked! Prompt:', prompt, 'isGenerating:', isGenerating, 'currentChatId:', currentChatId, 'isLegacyChat:', isLegacyChat);
    if (prompt.trim() && !isGenerating) {
      if (currentChatId && !isLegacyChat) {
        console.log('✅ Calling onFollowUp with prompt:', prompt);
        onFollowUp(prompt);
      } else {
        console.log('✅ Calling onGenerate with prompt:', prompt, isLegacyChat ? '(legacy chat - starting new)' : '(no current chat)');
        onGenerate(prompt);
      }
      setPrompt(''); // Clear prompt after sending
    } else {
      console.log('❌ Not calling generate/follow-up - prompt empty or already generating');
    }
  }

  const handleExampleClick = (example) => {
    setPrompt(example)
    setShowExamples(false) // Close dialog after selection
  }

  const handleRetryMessage = async (messageIndex) => {
    // Find the user message before the failed assistant message
    const userMessage = chatHistory[messageIndex - 1];
    if (userMessage && userMessage.role === 'user') {
      setRetryingMessageIndex(messageIndex);
      console.log(`🔄 Retrying message: "${userMessage.content}"`);
      
      try {
        // Call onFollowUp with the original user message
        await onFollowUp(userMessage.content);
        // If successful, the chat history will be updated by AppBuilder
        console.log('✅ Retry successful');
      } catch (error) {
        console.error('❌ Retry failed:', error);
        // The error handling in AppBuilder will add a new error message
      } finally {
        setRetryingMessageIndex(null);
      }
    }
  }

  // Enhanced auto-scroll to bottom - always scroll to latest message
  useEffect(() => {
    const scrollToBottom = () => {
      if (chatHistoryRef.current) {
        const element = chatHistoryRef.current;
        // Force scroll to bottom immediately
        element.scrollTop = element.scrollHeight;
        
        // Use requestAnimationFrame for smoother scrolling
        requestAnimationFrame(() => {
          if (chatHistoryRef.current) {
            chatHistoryRef.current.scrollTop = chatHistoryRef.current.scrollHeight;
          }
        });
      }
    };

    // Immediate scroll
    scrollToBottom();
    
    // Multiple delayed scrolls to handle dynamic content loading
    const timeoutId1 = setTimeout(scrollToBottom, 50);
    const timeoutId2 = setTimeout(scrollToBottom, 150);
    const timeoutId3 = setTimeout(scrollToBottom, 300);
    
    return () => {
      clearTimeout(timeoutId1);
      clearTimeout(timeoutId2);
      clearTimeout(timeoutId3);
    };
  }, [chatHistory, chatHistory.length])

  // Additional scroll triggers for window resize and generation state changes
  useEffect(() => {
    const scrollToBottom = () => {
      if (chatHistoryRef.current) {
        chatHistoryRef.current.scrollTop = chatHistoryRef.current.scrollHeight;
      }
    };
    
    scrollToBottom();
    
    const handleResize = () => {
      setTimeout(scrollToBottom, 100);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [isGenerating])

  return (
    <Flex direction="column" height="100%" gap="size-300" UNSAFE_style={{ padding: '0 8px' }}>
      <View UNSAFE_style={{ marginBottom: '8px' }}>
        <Flex direction="row" justifyContent="space-between" alignItems="center" marginBottom="size-100">
          <Heading level={3} UNSAFE_style={{ 
            fontSize: '18px', 
            fontWeight: '600', 
            color: '#111827',
            marginBottom: '0'
          }}>
            {currentChatId ? 'Continue Conversation' : 'Describe Your App'}
          </Heading>
          {currentChatId && (
            <ActionButton 
              isQuiet 
              onPress={onNewChat}
              UNSAFE_style={{ fontSize: '12px', color: '#6B7280' }}
            >
              🆕 New Chat
            </ActionButton>
          )}
        </Flex>

        {/* Current Chat Info */}
        {currentChatTitle && (
          <View 
            backgroundColor="blue-100"
            borderRadius="medium"
            padding="size-150"
            marginBottom="size-100"
          >
            <Flex direction="row" alignItems="center" gap="size-100">
              <Text UNSAFE_style={{ fontSize: '12px', color: '#1E40AF', fontWeight: '600' }}>
                💬 Current Chat:
              </Text>
              <Text UNSAFE_style={{ fontSize: '12px', color: '#1E40AF' }}>
                {currentChatTitle}
              </Text>
            </Flex>
          </View>
        )}

        <Text UNSAFE_style={{ 
          fontSize: '14px', 
          color: '#6B7280',
          lineHeight: '1.5'
        }}>
{currentChatId 
            ? 'Describe your changes or improvements to continue iterating on your app.'
            : 'Tell us what kind of application you\'d like to build, and our AI will generate the code for you.'
          }
        </Text>
      </View>

      {/* Chat History - Moved above prompt field */}
      {chatHistory.length > 0 && (
        <View marginBottom="size-200">
          <Heading level={4} UNSAFE_style={{ 
            fontSize: '16px', 
            fontWeight: '600', 
            color: '#111827',
            marginBottom: '12px'
          }}>
            💬 Chat History
          </Heading>
          <View 
            ref={chatHistoryRef}
            UNSAFE_style={{
              maxHeight: '400px',
              overflowY: 'auto',
              border: '1px solid #E5E7EB',
              borderRadius: '8px',
              background: '#F9FAFB'
            }}>
            {chatHistory.map((message, index) => (
              <View key={index} UNSAFE_style={{
                padding: '12px',
                borderBottom: index < chatHistory.length - 1 ? '1px solid #E5E7EB' : 'none',
                marginBottom: '4px'
              }}>
                <Flex direction="row" justifyContent="space-between" alignItems="flex-start">
                  <View flex={1}>
                    <Text UNSAFE_style={{
                      fontSize: '12px',
                      color: message.role === 'user' ? '#3B82F6' : (message.isError ? '#EF4444' : '#10B981'),
                      fontWeight: '600',
                      marginBottom: '4px'
                    }}>
                      {message.role === 'user' ? '👤 You' : (message.isError ? '⚠️ Assistant' : '🤖 Assistant')}
                      {message.isV0Message && ' (v0.dev)'}
                      {message.timestamp && (
                        <span style={{ fontSize: '10px', color: '#9CA3AF', fontWeight: 'normal', marginLeft: '8px' }}>
                          {new Date(message.timestamp).toLocaleTimeString()}
                        </span>
                      )}
                    </Text>
                    <Text UNSAFE_style={{
                      fontSize: '13px',
                      color: message.isError ? '#DC2626' : '#374151',
                      lineHeight: '1.5',
                      backgroundColor: message.isError ? '#FEF2F2' : 'transparent',
                      padding: message.isError ? '8px' : '4px',
                      borderRadius: message.isError ? '4px' : '0',
                      border: message.isError ? '1px solid #FECACA' : 'none',
                      wordWrap: 'break-word',
                      whiteSpace: 'pre-wrap',
                      overflowWrap: 'break-word',
                      display: 'block',
                      width: '100%'
                    }}>
                      {/* Show full message content for proper context */}
                      {message.content}
                    </Text>
                  </View>
                  
                  {/* Retry button for failed assistant messages */}
                  {message.isError && message.role === 'assistant' && index > 0 && (
                    <ActionButton
                      size="S"
                      variant="primary"
                      onPress={() => handleRetryMessage(index)}
                      isDisabled={retryingMessageIndex === index || isGenerating}
                      UNSAFE_style={{
                        marginLeft: '8px',
                        fontSize: '11px',
                        minWidth: '70px',
                        opacity: retryingMessageIndex === index ? 0.7 : 1
                      }}
                    >
                      {retryingMessageIndex === index ? '🔄 Retrying...' : '🔁 Retry'}
                    </ActionButton>
                  )}
                </Flex>
              </View>
            ))}
          </View>
        </View>
      )}
      
      <View UNSAFE_style={{
        border: '1px solid #E5E7EB',
        borderRadius: '8px',
        padding: '1px',
        background: 'white',
        boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.1)'
      }}>
        <TextArea
          placeholder={currentChatId 
            ? "Describe your changes or improvements..."
            : "Example: Create a todo app with add, edit, delete, and mark complete functionality. Include a clean, modern design with filters for all, active, and completed tasks."
          }
          value={prompt}
          onChange={setPrompt}
          height="size-2400"
          width="100%"
          isRequired
          UNSAFE_style={{
            border: 'none',
            borderRadius: '8px',
            fontSize: '14px',
            lineHeight: '1.5'
          }}
        />
      </View>

      {/* Legacy Chat Warning */}
      {isLegacyChat && (
        <Well marginTop="size-200" UNSAFE_style={{
          background: '#FEF3C7',
          border: '1px solid #F59E0B',
          borderRadius: '8px',
          padding: '12px'
        }}>
          <Text UNSAFE_style={{ fontSize: '14px', color: '#92400E' }}>
            ⚠️ This chat was created with the old system. Follow-ups are not supported. Your message will start a new chat.
          </Text>
        </Well>
      )}

      <Button 
        variant="accent" 
        onPress={handleSubmit}
        isDisabled={!prompt.trim() || isGenerating}
        alignSelf="stretch"
        UNSAFE_style={{
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          border: 'none',
          borderRadius: '8px',
          padding: '12px 24px',
          fontSize: '14px',
          fontWeight: '600',
          boxShadow: '0 4px 12px rgba(102, 126, 234, 0.4)',
          transition: 'all 0.2s ease-in-out'
        }}
      >
        <Flex direction="row" alignItems="center" gap="size-100">
          {isGenerating ? (
            <View UNSAFE_style={{ display: 'flex', alignItems: 'center', gap: '2px' }}>
              <style>{`
                @keyframes bounce {
                  0%, 80%, 100% { transform: scale(0.8); opacity: 0.5; }
                  40% { transform: scale(1.2); opacity: 1; }
                }
                .dot-1 { animation: bounce 1.4s infinite ease-in-out; animation-delay: 0s; }
                .dot-2 { animation: bounce 1.4s infinite ease-in-out; animation-delay: 0.2s; }
                .dot-3 { animation: bounce 1.4s infinite ease-in-out; animation-delay: 0.4s; }
              `}</style>
              <div className="dot-1" style={{ width: '4px', height: '4px', backgroundColor: 'white', borderRadius: '50%' }}></div>
              <div className="dot-2" style={{ width: '4px', height: '4px', backgroundColor: 'white', borderRadius: '50%' }}></div>
              <div className="dot-3" style={{ width: '4px', height: '4px', backgroundColor: 'white', borderRadius: '50%' }}></div>
            </View>
          ) : (
            <svg width="16" height="16" viewBox="0 0 24 24" fill="white">
              <path d="M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5"/>
            </svg>
          )}
          <Text UNSAFE_style={{ color: 'white', fontWeight: '600' }}>
            {isGenerating ? (
              retryStatus.attempt && retryStatus.maxAttempts 
                ? `${currentChatId && !isLegacyChat ? 'Updating' : 'Generating'} (${retryStatus.attempt}/${retryStatus.maxAttempts})` 
                : (currentChatId && !isLegacyChat ? 'Updating...' : 'Generating...')
            ) : (currentChatId && !isLegacyChat ? 'Send Follow-up' : 'Generate App')}
          </Text>
        </Flex>
      </Button>



      {/* Example Prompts - Expandable */}
      <View marginTop="size-200">
        <ActionButton 
          onPress={() => setShowExamples(!showExamples)}
          isQuiet
          UNSAFE_style={{
            fontSize: '14px',
            color: '#6B7280',
            padding: '8px',
            borderRadius: '6px',
            background: showExamples ? '#F3F4F6' : 'transparent',
            border: '1px solid #E5E7EB',
            width: '100%',
            textAlign: 'left'
          }}
        >
          <Flex direction="row" justifyContent="space-between" alignItems="center">
            <Text UNSAFE_style={{ fontSize: '14px', color: '#6B7280' }}>
              💡 Need inspiration? View example prompts
            </Text>
            <Text UNSAFE_style={{ fontSize: '12px', color: '#9CA3AF' }}>
              {showExamples ? '▲' : '▼'}
            </Text>
          </Flex>
        </ActionButton>
        
        {showExamples && (
          <View marginTop="size-100" UNSAFE_style={{
            border: '1px solid #E5E7EB',
            borderRadius: '8px',
            background: '#F9FAFB',
            padding: '12px'
          }}>
            <Flex direction="column" gap="size-150">
              {examples.map((example, index) => (
                <View 
                  key={index} 
                  UNSAFE_style={{
                    padding: '8px 12px',
                    background: '#FFFFFF',
                    borderRadius: '6px',
                    border: '1px solid #E5E7EB',
                    transition: 'all 0.2s ease-in-out',
                    cursor: 'pointer'
                  }}
                  UNSAFE_className="example-item"
                  onClick={() => handleExampleClick(example)}
                >
                  <style>{`
                    .example-item:hover {
                      border-color: #3B82F6 !important;
                      box-shadow: 0 2px 4px rgba(59, 130, 246, 0.1);
                    }
                  `}</style>
                  <Text UNSAFE_style={{ 
                    fontSize: '13px',
                    lineHeight: '1.4',
                    color: '#374151'
                  }}>
                    {example}
                  </Text>
                </View>
              ))}
            </Flex>
          </View>
        )}
      </View>
    </Flex>
  )
}

export default PromptPanel